@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'DS-Digital';
  src: url(/ra-lifecycle-http-watches/static/media/DS-DIGIT.063ff1f7.TTF);
}

body {
  background-color: #000;
  color: #fff;
}

.container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  margin: 50px auto;
  width: 700px;
}

.watches-form {
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-end;
  margin-bottom: 20px;
}

.watches-form label {
  display: block;
  width: 40%;
}

.watches-form input,
.watches-form__submit {
  height: 40px;
  border: 2px solid darkmagenta;
  border-radius: 5px;
  background-color: lightgray;
}

.watches-form input {
  width: calc(100% - 20px);
  margin-top: 10px;
  margin-right: 20px;
  padding: 0 10px;
}

.watches-form__submit {
  width: 20%;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
}

.watches__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.watches-list__item {
  min-width: 172px;
  padding: 10px 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px magenta;
  text-align: center;
  margin-bottom: 20px;
}

.watches-list__item-name {
  text-align: left;
}

.watches-list__item-time {
  font-family: 'DS-Digital';
  font-size: 2em;
  color: magenta;
  text-shadow: magenta 0 0 3px;
}

.watches-list__item-delete {
  float: right;
  cursor: pointer;
}

